html {
	font-size: 62.5%;
	box-sizing: border-box;
}
*,
*:before,
*:after {
	box-sizing: inherit;
}
img {
	max-width: 100%;
	// 	height: auto;
	// 	// display: block;
}
audio {
	display: none;
}
a {
	text-decoration: none;
}
ul {
	list-style: none;
	padding: 0;
	margin: 0;
}
.contenedor {
	width: 95%;
	max-width: 120rem;
	margin: 0 auto;
}
body {
	font-size: 1.6rem;
}
div.slides {
	zoom: normal !important;
}
