@use "base/mixins" as m;
@use "base/variables" as v;

// Precaucion
.precaucion {
	img:nth-child(1) {
		height: 13rem;
	}
	h3 {
		color: #f7fafc;
		font-weight: 700;
		color: transparent;
		-webkit-text-stroke: 0.5rem v.$blanco;
	}
	img:not(img:nth-child(1)) {
		height: 4rem;
	}
	pre {
		color: #f1f1f1;
	}
}

.intro {
	h3 {
		font-style: italic;
		color: v.$blanco;
		-webkit-text-stroke: 0.3rem #0b0e48;
		// font-weight: 700;
	}
}
.titulo {
	.contenedor-texto {
		h4 {
			color: #e8f538;
		}
		#camaxtli {
			height: 10rem;
		}
		h1 {
			color: v.$blanco;
		}
		#esc {
			height: 3.5rem;
		}
	}
}

.leyenda {
	// background-color: red;
	width: 100%;
	height: 15em;
	position: relative;
}
[class^="tex"] {
	border-radius: 5rem 0 5rem 0;
	display: block;
	background-color: rgba(0, 0, 0, 0.309);
	width: 100%;
	padding: 1rem;
	box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);

	p[class^="item"] {
		margin: 0;
		width: 100% !important;
		font-size: 2.5rem;
		span {
			display: block;
		}
	}
}

.sec-1 {
	display: flex !important;
	flex-direction: column;
	justify-content: space-between;

	.img-1-1 {
		position: fixed;
		right: 0;
		transform: translateX(40%);
	}
	.tex-1-1 {
		transform: translate(-20%, -20%);
	}
	.tex-1-2 {
		transform: v.$left-30;
		width: 60%;
	}
	.tex-1-3 {
		transform: translate(-20%, 0);
	}
}

.sec-2 {
	display: flex !important;
	align-items: center;

	[class^="tex"] {
		height: 50%;
		display: flex;
		flex-direction: column;
		justify-content: center;

		p {
			line-height: 2;
		}
	}
	.tex-2-1 {
		transform: v.$left-30;
	}
	.tex-2-2 {
		transform: v.$right-30;
		p {
			color: v.$negro;
		}
	}
}
.sec-3 {
	display: flex !important;
	align-items: center;

	.tex-3-1 {
		transform: v.$left-30;
	}
	.tex-3-2 {
		transform: v.$right-30;
	}
}

.sec-4 {
	display: flex !important;
	flex-direction: column;
	justify-content: space-between;

	p {
		line-height: 2;
	}

	.camxtli_img {
		position: fixed !important;
		left: 50%;
		top: 50%;
		@include m.zoomAnimate(-50%, -50%);
	}
}

.sec-5 {
	display: flex !important;
	flex-direction: column;
	justify-content: space-between;

	.tex-5-1 {
		background-color: transparent;
		padding: 0;
		box-shadow: none;
		display: flex;
		justify-content: space-between;
		.tex-5-1-1 {
			transform: v.$left-30;
		}
		.tex-5-1-2 {
			transform: v.$right-30;
		}
	}
	.tex-5-2 {
		transform: translate(0, 30%);
	}
}
.sec-6 {
	display: grid !important;
	grid-template: repeat(5, 1fr) / repeat(4, 1fr);
	.tex-6-1,
	.tex-6-2 {
		display: flex;
		align-items: center;
		height: auto;

		p {
			line-height: 2;
		}
	}
	.tex-6-1 {
		grid-column: 2 / 5;
		grid-row: 1 / 3;
		transform: v.$right-30;
	}
	.tex-6-2 {
		grid-column: 1 / 4;
		grid-row: 4 / 6;
		transform: v.$left-30;
	}
}
.sec-7 {
	@extend .sec-6;
	.tex-7-1,
	.tex-7-2 {
		display: flex;
		align-items: center;
		height: auto;

		p {
			line-height: 2;
		}
	}
	.tex-7-1 {
		grid-column: 1 / 6;
		grid-row: 1 / 3;
		transform: translate(-10%, 0);
	}
	.tex-7-2 {
		grid-column: 2 / 5;
		grid-row: 5 / 6;
		transform: v.$right-30;
	}
}
.sec-8 {
	display: flex !important;
	align-items: flex-start;
	.tex-8-1,
	.tex-8-2 {
		display: flex;
		align-items: center;
		height: 30%;
	}
	.tex-8-1 {
		transform: v.$left-30;
	}

	.tex-8-2 {
		transform: v.$right-30;
	}
}

.sec-9 {
	@extend .sec-5;

	.tex-9-1 {
		@extend .tex-5-1;

		.tex-9-1-1 {
			@extend .tex-5-1-1;
		}
		.tex-9-1-2 {
			@extend .tex-5-1-2;
		}
	}
	.tex-9-2 {
		@extend .tex-5-2;
	}
}
.sec-10 {
	display: flex !important;
	flex-direction: column;
	justify-content: space-between;

	.tex-10-1,
	.tex-10-2,
	.tex-10-3 {
		transform: translate(70%, 0);
		width: 70%;
	}
}

.sec-12 {
	display: flex !important;
	align-items: flex-start;

	.tex-12-1 {
		transform: v.$left-30;
	}
	.tex-12-2 {
		transform: v.$right-30;
	}
}
