@use "base/variables" as v;
@use "base/mixins" as m;

.inicio,
.menu {
	width: 100%;
	height: 100vh;
}

.inicio {
	background: url(../img/leyenda1/i11y1.png) no-repeat 50% 50%;
	background-size: cover;
}

/* --- Titulo ---  */
.inicio_texto {
	font-family: v.$fuente3;
	color: v.$blanco;
	font-size: 4.5rem;
	letter-spacing: 1rem;
	font-weight: v.$bold;
	position: absolute;
	top: 50%;
	left: 50%;
	text-align: center;
	transform: translate(-50%, -50%);
}

/* Menu de navegacion */
.menu {
	z-index: 0;
	position: absolute;
	background: v.$negro;
	top: -100%;
}
