@use "base/variables" as v;

/* Titulos */
.data {
	padding: 8rem 0 0 4rem;
	text-align: left;
	li {
		a {
			color: v.$blanco;
			font-family: v.$fuente2;
			transition: 0.3s ease-in-out;
			position: relative;

			&:hover {
				color: lighten(v.$secundario, 30%);

				&::before {
					width: 100%;
				}
			}

			&::before {
				position: absolute;
				content: "";
				bottom: 0;
				left: 50%;
				transform: translate(-50%) translateY(50%);
				display: block;
				width: 0%;
				height: 1px;
				background-color: v.$secundario;
				transition: 0.3s ease-in-out;
			}
		}

		&:first-child {
			color: v.$gris;
			font-family: v.$fuente2;
			font-size: 3rem;
			text-transform: capitalize;
		}

		&:not(:first-child) {
			color: v.$blanco;
			text-transform: uppercase;
			font-size: 4.2rem;
		}
	}
}
