@use "variables" as v;

@mixin telefono {
	@media (min-width: v.$telefono) {
		@content;
	}
}

@mixin tablet {
	@media (min-width: v.$tablet) {
		@content;
	}
}

@mixin desktop {
	@media (min-width: v.$desktop) {
		@content;
	}
}

@mixin grid($columnas, $separacion) {
	display: grid;
	grid-template-columns: repeat($columnas, 1fr);
	gap: $separacion;
}

@mixin zoomAnimate($x: 0, $y: 0, $z: "") {
	animation: zoom 3s ease-in-out infinite alternate;

	@keyframes zoom {
		0% {
			transform: scale(0.9) translate($x, $y);
		}
		100% {
			transform: scale(1.1) translate($x, $y);
		}
	}
}
