@use "base/variables" as v;

/* --- Btn ham ---  */

.toggle-btn {
	position: fixed;
	margin: 2rem;
	width: 4rem;
	padding-top: 1rem;
	height: 1.2rem;
	right: 0;
	z-index: 1;

	/* --- Btn ham hover ---  */
	&:hover {
		cursor: pointer;	
	}
	
	/* Lineas del btn ham */
	span.one,
	span.two {
		position: absolute;
		width: 4rem;
		height: 0.2rem;
		background: v.$blanco;
		box-shadow: 1px 1px 5px 0px v.$negro;
	}

	span.two {
		margin-top: 1.2rem;
	}
}